body{
  background-image: url('allseasons.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}

.App {
  text-align: center;
}

.hidden{
  display: none;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.brochure, main {
  opacity: .9;
  margin: 20px;
  padding:10px;
  border: 2px solid black;
  background-color: #666;
}

main{
  padding-top: 20px;
}

.forecast-container{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.forecast{
  opacity: .9;
  margin: 20px;
  padding:10px;
  border: 2px solid black;
  background-color: #f3f3f3;
}

.main-data, .quote-container, .about-container {
  opacity: .9;
  margin: 20px;
  padding:10px;
  border: 2px solid black;
  background-color: #f3f3f3;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



